@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../source/fonts/ptsans.woff2") format("woff2"), url("../source/fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../source/fonts/ptsansbold.woff2") format("woff2"), url("../source/fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

body {
  min-height: 100vh;
  color: #333333;
  background-color: #e7e5e5;
  min-width: 320px;
  text-align: center;
  font-family: "PT Sans", sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.5;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.advantages__title {
  color: #000000;
  font-size: 18px;
  line-height: 1.44;
  margin: 0;
  padding: 34px 25px 35px;
}

.advantages__description {
  margin: 0;
  padding: 0 18px 33px 18px;
}

.advantages--ladder {
  position: relative;
}

.advantages--ladder::before {
  position: absolute;
  content: "";
  background: url("../img/mobile/ladder.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 768px;
  height: 37px;
  bottom: -39px;
  right: 50%;
  transform: translate(50%, -50%);
}

@media (min-width: 768px) {
  .advantages__title {
    margin: auto;
    padding: 48px 82px 0;
    font-size: 21px;
    line-height: 1.24;
  }

  .advantages--ladder::before {
    display: none;
  }

  .advantages__description {
    padding-top: 30px;
    margin: 0;
    padding-bottom: 55px;
  }
}
@media (min-width: 1200px) {
