.main-nav {
  position: relative;
}

.main-nav__list {
  padding: 0;
  background-color: #81b3d2;
  margin: 0 auto;
  flex-wrap: wrap;
}

.main-nav__toggle {
  position: absolute;
  display: block;
  right: 20px;
  z-index: 2;
  top: 15px;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.main-nav,
.main-nav--opened {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  color: #ffffff;
  background-color: #ffffff;
}

.main-nav--closed .main-nav__closed,
.main-nav--opened .main-nav__opened {
  display: none;
}

.main-nav--closed .main-nav__closed {
  display: none;
}

.main-nav__opened:hover,
.main-nav__closed:hover {
  opacity: 60%;
}

.main-nav__opened:active,
.main-nav__closed:active {
  opacity: 30%;
}

.main-nav--opened .site-list__item:last-child::before {
  content: "";
  position: absolute;
  top: 213px;
  left: 0;
  z-index: 2;
  width: 120vw;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list,
.main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav--opened .site-list__item:last-child::before {
    display: none;
  }

  .main-nav__list {
    background-color: #ffffff;
    display: flex;
    justify-content: space-between;
    min-height: 56px;
    top: 0;
    margin: 0;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .main-nav--opened .site-list__item:last-child::before {
    display: none;
  }
}
